// eslint-disable-next-line import/no-cycle
import {
  CLIENT_UPDATE_FILTERS,
  CLIENT_GET_USERS,
  CLIENT_GET_USERS_SUCCESS,
  CLIENT_GET_USERS_ERROR,
  CLIENT_GET_BY_ID,
  CLIENT_GET_BY_ID_SUCCESS,
  CLIENT_GET_BY_ID_ERROR,
} from '../actions';

const INIT_STATE = {
  clients: {
    data: [],
    totalItems: 0,
    currentPage: 1,
    totalPage: 0,
  },
  currentClient: {
    first_name: '',
    last_name: '',
    email: '',
    phone_number: '',
    date_of_birth: '',
    gender: '',
    address: '',
    city: '',
    state: '',
  },
  currentClientServices: {
    data: {
      email: '',
      services: [],
    },
    totalItems: 1,
    currentPage: 1,
    totalPage: 1,
  },
  filters: {
    currentPage: 1,
    selectedPageSize: 8,
    selectedOrderOption: { column: 'email', label: 'Email' },
    search: '',
  },
  accessToken: '',
  loading: false,
  message: '',
  error: '',
};

export default (state = INIT_STATE, action) => {
  switch (action.type) {
    case CLIENT_UPDATE_FILTERS:
      return {
        ...state,
        filters: action.payload.filters,
      };
    case CLIENT_GET_USERS:
      return {
        ...state,
        loading: true,
        accessToken: action.payload.accessToken,
        message: '',
        error: '',
      };
    case CLIENT_GET_USERS_SUCCESS:
      return {
        ...state,
        clients: action.payload.data,
        loading: false,
        message: '',
      };
    case CLIENT_GET_USERS_ERROR:
      return {
        ...state,
        loading: false,
        error: action.payload.message,
      };

    case CLIENT_GET_BY_ID:
      return {
        ...state,
        loading: true,
        accessToken: action.payload.accessToken,
        message: '',
        error: '',
      };
    case CLIENT_GET_BY_ID_SUCCESS:
      return {
        ...state,
        currentClient: action.payload.client,
        currentClientServices: action.payload.clientServices,
        loading: false,
        message: '',
      };
    case CLIENT_GET_BY_ID_ERROR:
      return {
        ...state,
        loading: false,
        error: action.payload.message,
      };

    default:
      return { ...state };
  }
};
