import { all, call, fork, put, takeEvery } from 'redux-saga/effects';
import instance from '../../helpers/axiosInstance';

import { SUB_CATEGORY_ALL_GET } from '../actions';

import { getAllSubCategorySuccess, getAllSubCategoryError } from './actions';

const getSubCategoryAsync = async (accessToken, filters) => {
  const response = await instance({
    url: `/client/get-subcategory?pageSize=${filters.selectedPageSize}&currentPage=${filters.currentPage}&search=${filters.search}`,
    method: 'GET',
    headers: {
      Authorization: accessToken,
    },
  });

  return { ...response.data };
};

function* getSubCategory({ payload }) {
  const { accessToken, filters } = payload;
  try {
    const subcategory = yield call(getSubCategoryAsync, accessToken, filters);

    if (subcategory.success) {
      yield put(getAllSubCategorySuccess(subcategory.data));
    } else {
      yield put(getAllSubCategoryError('Error getting Sub Category'));
    }
  } catch (error) {
    yield put(getAllSubCategoryError('Error getting Sub Category'));
  }
}

export function* watchGetSubCategory() {
  yield takeEvery(SUB_CATEGORY_ALL_GET, getSubCategory);
}

export default function* rootSaga() {
  yield all([fork(watchGetSubCategory)]);
}
