// eslint-disable-next-line import/no-cycle
import {
  CATEGORY_ALL_GET,
  CATEGORY_ALL_GET_ERROR,
  CATEGORY_ALL_GET_SUCCESS,
  CATEGORY_UPDATE_FILTERS,
} from '../actions';

export const updateCategoryFilters = (filters) => ({
  type: CATEGORY_UPDATE_FILTERS,
  payload: { filters },
});
export const getAllCategory = (accessToken, filters) => ({
  type: CATEGORY_ALL_GET,
  payload: { accessToken, filters },
});
export const getAllCategorySuccess = (data) => ({
  type: CATEGORY_ALL_GET_SUCCESS,
  payload: { data },
});
export const getAllCategoryError = (message) => ({
  type: CATEGORY_ALL_GET_ERROR,
  payload: { message },
});
