// eslint-disable-next-line import/no-cycle
import {
  CATEGORY_ALL_GET,
  CATEGORY_ALL_GET_ERROR,
  CATEGORY_ALL_GET_SUCCESS,
  CATEGORY_UPDATE_FILTERS,
} from '../actions';

const INIT_STATE = {
  category: {
    data: [],
    totalItems: 0,
    totalPage: 0,
  },
  filters: {
    search: '',
    currentPage: 1,
    selectedPageSize: 8,
  },
  loading: false,
  error: '',
};

export default (state = INIT_STATE, action) => {
  switch (action.type) {
    case CATEGORY_UPDATE_FILTERS:
      return {
        ...state,
        filters: action.payload.filters,
      };

    case CATEGORY_ALL_GET:
      return {
        ...state,
        loading: true,
        error: '',
      };

    case CATEGORY_ALL_GET_SUCCESS:
      return {
        ...state,
        category: action.payload.data,
        loading: false,
        error: '',
      };

    case CATEGORY_ALL_GET_ERROR:
      return {
        ...state,
        loading: false,
        error: action.payload.message,
      };

    default:
      return { ...state };
  }
};
