import { all } from 'redux-saga/effects';
import authSagas from './auth/saga';
import todoSagas from './todo/saga';
import chatSagas from './chat/saga';
import surveyListSagas from './surveyList/saga';
import surveyDetailSagas from './surveyDetail/saga';

import accountSagas from './account/saga';
import statusSagas from './status/saga';
import serviceSagas from './service/saga';
import categorySagas from './category/saga';
import subCategorySagas from './sub-category/saga';
import clientsSagas from './client/saga';

export default function* rootSaga() {
  yield all([
    authSagas(),
    accountSagas(),
    statusSagas(),
    serviceSagas(),
    categorySagas(),
    subCategorySagas(),
    clientsSagas(),
    todoSagas(),
    chatSagas(),
    surveyListSagas(),
    surveyDetailSagas(),
  ]);
}
