// eslint-disable-next-line import/no-cycle
import {
  SERVICE_ALL_GET,
  SERVICE_ALL_GET_ERROR,
  SERVICE_ALL_GET_SUCCESS,
  SERVICE_UPDATE_FILTERS,
} from '../actions';

const INIT_STATE = {
  service: {
    data: [],
    totalItems: 0,
    totalPage: 0,
  },
  filters: {
    search: '',
    currentPage: 1,
    selectedPageSize: 8,
    selectedOrderOption: {
      column: 'disabled',
      label: 'Disabled',
    },
  },
  loading: false,
  error: '',
};

export default (state = INIT_STATE, action) => {
  switch (action.type) {
    case SERVICE_UPDATE_FILTERS:
      return {
        ...state,
        filters: action.payload.filters,
      };

    case SERVICE_ALL_GET:
      return {
        ...state,
        loading: true,
        error: '',
      };

    case SERVICE_ALL_GET_SUCCESS:
      return {
        ...state,
        service: action.payload.data,
        loading: false,
        error: '',
      };

    case SERVICE_ALL_GET_ERROR:
      return {
        ...state,
        loading: false,
        error: action.payload.message,
      };

    default:
      return { ...state };
  }
};
