// eslint-disable-next-line import/no-cycle
import {
  SERVICE_ALL_GET,
  SERVICE_ALL_GET_ERROR,
  SERVICE_ALL_GET_SUCCESS,
  SERVICE_UPDATE_FILTERS,
} from '../actions';

export const updateServiceFilters = (filters) => ({
  type: SERVICE_UPDATE_FILTERS,
  payload: { filters },
});
export const getAllService = (accessToken, filters) => ({
  type: SERVICE_ALL_GET,
  payload: { accessToken, filters },
});
export const getAllServiceSuccess = (data) => ({
  type: SERVICE_ALL_GET_SUCCESS,
  payload: { data },
});
export const getAllServiceError = (message) => ({
  type: SERVICE_ALL_GET_ERROR,
  payload: { message },
});
