import { all, call, fork, put, takeEvery } from 'redux-saga/effects';
import instance from '../../helpers/axiosInstance';

import { SERVICE_ALL_GET } from '../actions';

import { getAllServiceError, getAllServiceSuccess } from './actions';

const getServiceAsync = async (accessToken, filters) => {
  const response = await instance({
    url: `/client/get-service?search=${filters.search}&pageSize=${filters.selectedPageSize}&currentPage=${filters.currentPage}&orderBy=${filters.selectedOrderOption.column}`,
    method: 'GET',
    headers: {
      Authorization: accessToken,
    },
  });

  return { ...response.data };
};

function* getService({ payload }) {
  const { accessToken, filters } = payload;
  try {
    const service = yield call(getServiceAsync, accessToken, filters);
    if (service.success) {
      yield put(getAllServiceSuccess(service.data));
    } else {
      yield put(getAllServiceError('Error getting Status'));
    }
  } catch (error) {
    yield put(getAllServiceError('Error getting Status'));
  }
}

export function* watchGetService() {
  yield takeEvery(SERVICE_ALL_GET, getService);
}

export default function* rootSaga() {
  yield all([fork(watchGetService)]);
}
