import { combineReducers } from 'redux';
import settings from './settings/reducer';
import menu from './menu/reducer';
import authUser from './auth/reducer';
import todoApp from './todo/reducer';
import chatApp from './chat/reducer';
import surveyListApp from './surveyList/reducer';
import surveyDetailApp from './surveyDetail/reducer';

import accountsApp from './account/reducer';
import statusApp from './status/reducer';
import serviceApp from './service/reducer';
import categoryApp from './category/reducer';
import subCategoryApp from './sub-category/reducer';
import clientsApp from './client/reducer';

const reducers = combineReducers({
  menu,
  settings,
  authUser,
  accountsApp,
  statusApp,
  serviceApp,
  categoryApp,
  subCategoryApp,
  clientsApp,
  todoApp,
  chatApp,
  surveyListApp,
  surveyDetailApp,
});

export default reducers;
