/* eslint-disable global-require */
import './static/css/vendor/bootstrap.min.css';
import './static/css/vendor/bootstrap.rtl.only.min.css';
import 'react-circular-progressbar/dist/styles.css';
import 'react-perfect-scrollbar/dist/css/styles.css';
import 'react-big-calendar/lib/css/react-big-calendar.css';
import 'react-image-lightbox/style.css';
import 'video.js/dist/video-js.css';
import { defaultColor, isDarkSwitchActive } from './constants/defaultValues';
import { getCurrentColor, setCurrentColor } from './helpers/Utils';

const color = isDarkSwitchActive ? getCurrentColor() : defaultColor;

const render = () => {
  import(`./static/css/sass/themes/${color}.scss`).then(() => {
    require('./AppRenderer');
  });
};

require('./AppRenderer');

setCurrentColor(color);

render();
