import { all, call, fork, put, takeEvery } from 'redux-saga/effects';
import instance from '../../helpers/axiosInstance';

import { CATEGORY_ALL_GET } from '../actions';

import { getAllCategorySuccess, getAllCategoryError } from './actions';

const getCategoryAsync = async (accessToken, filters) => {
  const response = await instance({
    url: `/client/get-category?pageSize=${filters.selectedPageSize}&currentPage=${filters.currentPage}&search=${filters.search}`,
    method: 'GET',
    headers: {
      Authorization: accessToken,
    },
  });

  return { ...response.data };
};

function* getCategory({ payload }) {
  const { accessToken, filters } = payload;
  try {
    const subcategory = yield call(getCategoryAsync, accessToken, filters);

    if (subcategory.success) {
      yield put(getAllCategorySuccess(subcategory.data));
    } else {
      yield put(getAllCategoryError('Error getting Category'));
    }
  } catch (error) {
    yield put(getAllCategoryError('Error getting Category'));
  }
}

export function* watchGetCategory() {
  yield takeEvery(CATEGORY_ALL_GET, getCategory);
}

export default function* rootSaga() {
  yield all([fork(watchGetCategory)]);
}
