import { all, call, fork, put, takeEvery } from 'redux-saga/effects';

import {
  LOGIN_USER,
  REGISTER_USER,
  LOGOUT_USER,
  FORGOT_PASSWORD,
  RESET_PASSWORD,
  VERIFY_OTP,
} from '../actions';

import {
  loginUserSuccess,
  loginUserError,
  registerUserSuccess,
  registerUserError,
  forgotPasswordSuccess,
  forgotPasswordError,
  resetPasswordSuccess,
  resetPasswordError,
  verifyOTPSuccess,
  verifyOTPError,
} from './actions';

import {
  adminRoot,
  currentUser,
  defaultWaitTime,
} from '../../constants/defaultValues';

import {
  setAccessToken,
  setAllowOTP,
  setCurrentUser,
  wait,
} from '../../helpers/Utils';

import instance from '../../helpers/axiosInstance';
import { setUserRole } from '../../helpers/authHelper';

const loginWithEmailPasswordAsync = async (email, password) => {
  const response = await instance({
    url: '/auth/login',
    method: 'POST',
    data: { email, password },
  });

  return { ...response.data };
};

function* loginWithEmailPassword({ payload }) {
  const { email, password } = payload.user;
  const { history } = payload;

  try {
    const loginUser = yield call(loginWithEmailPasswordAsync, email, password);

    if (loginUser.success) {
      yield put(loginUserSuccess('otp_sent'));
      const item = { active: true, email };
      setAllowOTP(item);
      wait(defaultWaitTime, () => {
        history.push('/user/verify-otp');
      });
    } else {
      yield put(loginUserError(loginUser.error));
    }
  } catch (error) {
    yield put(loginUserError(error));
  }
}

const verifyOneTimePasswordAsync = async (email, otp) => {
  const response = await instance({
    url: '/auth/verify-otp',
    method: 'POST',
    data: { email, otp },
  });

  return { ...response.data };
};

function* verifyOneTimePassword({ payload }) {
  const { email, otp } = payload.user;
  const { history } = payload;

  try {
    const verifiedOTP = yield call(verifyOneTimePasswordAsync, email, otp);
    if (verifiedOTP.success) {
      const item = { ...currentUser, ...setUserRole(verifiedOTP.data) };
      yield put(verifyOTPSuccess(item, 'logged_in', verifiedOTP.token));
      wait(defaultWaitTime, () => {
        setCurrentUser({ ...item, access_token: verifiedOTP.token });
        setAccessToken(verifiedOTP.token);
        setAllowOTP({ active: false });
        history.push(adminRoot);
      });
    } else {
      yield put(verifyOTPError(verifiedOTP.error));
    }
  } catch (error) {
    yield put(verifyOTPError(error));
  }
}

export function* watchRegisterUser() {
  // eslint-disable-next-line no-use-before-define
  yield takeEvery(REGISTER_USER, registerWithEmailPassword);
}

const registerWithEmailPasswordAsync = async (email, password) => {
  return { email, password };
};

function* registerWithEmailPassword({ payload }) {
  const { email, password } = payload.user;
  const { history } = payload;
  try {
    const registerUser = yield call(
      registerWithEmailPasswordAsync,
      email,
      password
    );
    if (!registerUser.message) {
      const item = { uid: registerUser.user.uid, ...currentUser };
      setCurrentUser(item);
      yield put(registerUserSuccess(item));
      history.push(adminRoot);
    } else {
      yield put(registerUserError(registerUser.message));
    }
  } catch (error) {
    yield put(registerUserError(error));
  }
}

export function* watchLogoutUser() {
  // eslint-disable-next-line no-use-before-define
  yield takeEvery(LOGOUT_USER, logout);
}

const logoutAsync = async () => {
  await instance({
    url: '/auth/logout',
    method: 'GET',
  });
};

function* logout({ payload }) {
  const { history } = payload;
  yield call(logoutAsync);
  wait(800, () => {
    setCurrentUser();
    setAccessToken();
    history.push(adminRoot);
  });
}

export function* watchForgotPassword() {
  // eslint-disable-next-line no-use-before-define
  yield takeEvery(FORGOT_PASSWORD, forgotPassword);
}

const forgotPasswordAsync = async (email) => {
  return { email };
};

function* forgotPassword({ payload }) {
  const { email } = payload.forgotUserMail;
  try {
    const forgotPasswordStatus = yield call(forgotPasswordAsync, email);
    if (!forgotPasswordStatus) {
      yield put(forgotPasswordSuccess('success'));
    } else {
      yield put(forgotPasswordError(forgotPasswordStatus.message));
    }
  } catch (error) {
    yield put(forgotPasswordError(error));
  }
}

export function* watchResetPassword() {
  // eslint-disable-next-line no-use-before-define
  yield takeEvery(RESET_PASSWORD, resetPassword);
}

const resetPasswordAsync = async (accessToken, oldPassword, newPassword) => {
  const response = await instance({
    url: '/auth/reset-password',
    method: 'PUT',
    data: {
      old_password: oldPassword,
      new_password: newPassword,
      confirm_password: newPassword,
    },
    headers: {
      Authorization: accessToken,
    },
  });

  return { ...response.data };
};

function* resetPassword({ payload }) {
  const { accessToken, oldPassword, newPassword } = payload;
  try {
    const resetPasswordStatus = yield call(
      resetPasswordAsync,
      accessToken,
      oldPassword,
      newPassword
    );
    if (resetPasswordStatus.success) {
      yield put(resetPasswordSuccess('reset-success'));
    } else {
      yield put(resetPasswordError(resetPasswordStatus.error));
    }
  } catch (error) {
    yield put(resetPasswordError(error));
  }
}

export function* watchLoginUser() {
  yield takeEvery(LOGIN_USER, loginWithEmailPassword);
}

export function* watchVerifyOTP() {
  yield takeEvery(VERIFY_OTP, verifyOneTimePassword);
}

export default function* rootSaga() {
  yield all([
    fork(watchLoginUser),
    fork(watchVerifyOTP),
    fork(watchLogoutUser),
    fork(watchRegisterUser),
    fork(watchForgotPassword),
    fork(watchResetPassword),
  ]);
}
