// eslint-disable-next-line import/no-cycle
import {
  STATUS_ALL_GET,
  STATUS_ALL_GET_ERROR,
  STATUS_ALL_GET_SUCCESS,
  STATUS_UPDATE_FILTERS,
} from '../actions';

export const updateStatusFilters = (filters) => ({
  type: STATUS_UPDATE_FILTERS,
  payload: { filters },
});
export const getAllStatus = (accessToken, filters) => ({
  type: STATUS_ALL_GET,
  payload: { accessToken, filters },
});
export const getAllStatusSuccess = (data) => ({
  type: STATUS_ALL_GET_SUCCESS,
  payload: { data },
});
export const getAllStatusError = (message) => ({
  type: STATUS_ALL_GET_ERROR,
  payload: { message },
});
