// eslint-disable-next-line import/no-cycle
import { getAllUsersData } from '../../helpers/Utils';
import {
  ACCOUNT_UPDATE_FILTERS,
  ACCOUNT_GET_USERS,
  ACCOUNT_GET_USERS_SUCCESS,
  ACCOUNT_GET_USERS_ERROR,
} from '../actions';

const INIT_STATE = {
  allUsersData: {
    data: getAllUsersData() || [],
    totalItems: 0,
    currentPage: 1,
    totalPage: 0,
  },
  currentUserData: null,
  filters: {
    currentPage: 1,
    selectedOrderOption: {
      column: 'email',
      label: 'Email',
    },
    selectedPageSize: 8,
    search: '',
  },
  accessToken: '',
  loading: false,
  message: '',
  error: '',
};

export default (state = INIT_STATE, action) => {
  switch (action.type) {
    case ACCOUNT_UPDATE_FILTERS:
      return {
        ...state,
        filters: action.payload.filters,
      };
    case ACCOUNT_GET_USERS:
      return {
        ...state,
        loading: true,
        accessToken: action.payload.accessToken,
        message: '',
        error: '',
      };
    case ACCOUNT_GET_USERS_SUCCESS:
      return {
        ...state,
        allUsersData: action.payload.data,
        loading: false,
        message: '',
      };
    case ACCOUNT_GET_USERS_ERROR:
      return {
        ...state,
        loading: false,
        error: action.payload.message,
      };

    default:
      return { ...state };
  }
};
