import { all, call, fork, put, takeEvery } from 'redux-saga/effects';
import instance from '../../helpers/axiosInstance';

import { CLIENT_GET_BY_ID, CLIENT_GET_USERS } from '../actions';

import {
  getClientsSuccess,
  getClientsError,
  getClientSuccess,
  getClientError,
} from './actions';

const getClientsAsync = async (accessToken, filters) => {
  const response = await instance({
    url: `/client/get-client?pageSize=${filters.selectedPageSize}&orderBy=${filters.selectedOrderOption.column}&currentPage=${filters.currentPage}&search=${filters.search}`,
    method: 'GET',
    headers: {
      Authorization: accessToken,
    },
  });

  return { ...response.data };
};

function* getClients({ payload }) {
  const { accessToken, filters } = payload;
  try {
    const clients = yield call(getClientsAsync, accessToken, filters);
    yield put(getClientsSuccess(clients.data));
  } catch (error) {
    yield put(getClientsError('Error getting Clients'));
  }
}

const getClientAsync = async (accessToken, id) => {
  const response = await instance({
    url: `/client/get-client/${id}`,
    method: 'GET',
    headers: {
      Authorization: accessToken,
    },
  });

  return { ...response.data };
};
const getClientMoreAsync = async (accessToken, id) => {
  const response = await instance({
    url: `/client/get-client-service/${id}`,
    method: 'GET',
    headers: {
      Authorization: accessToken,
    },
  });

  return { ...response.data };
};

function* getClient({ payload }) {
  const { accessToken, id } = payload;
  try {
    const [client, clientServices] = yield all([
      call(getClientAsync, accessToken, id),
      call(getClientMoreAsync, accessToken, id),
    ]);

    yield put(getClientSuccess(client.data[0], clientServices.data));
  } catch (error) {
    yield put(getClientError('Error getting Client'));
  }
}

export function* watchGetClients() {
  yield takeEvery(CLIENT_GET_USERS, getClients);
}
export function* watchGetClient() {
  yield takeEvery(CLIENT_GET_BY_ID, getClient);
}

export default function* rootSaga() {
  yield all([fork(watchGetClients), fork(watchGetClient)]);
}
