import { all, call, fork, put, takeEvery } from 'redux-saga/effects';
import instance from '../../helpers/axiosInstance';

import { STATUS_ALL_GET } from '../actions';

import { getAllStatusSuccess, getAllStatusError } from './actions';

const getStatusAsync = async (accessToken, filters) => {
  const response = await instance({
    url: `/client/get-status?search=${filters.search}&pageSize=${filters.selectedPageSize}&currentPage=${filters.currentPage}`,
    method: 'GET',
    headers: {
      Authorization: accessToken,
    },
  });

  return { ...response.data };
};

function* getStatus({ payload }) {
  const { accessToken, filters } = payload;
  try {
    const status = yield call(getStatusAsync, accessToken, filters);
    if (status.success) {
      yield put(getAllStatusSuccess(status.data));
    } else {
      yield put(getAllStatusError('Error getting Status'));
    }
  } catch (error) {
    yield put(getAllStatusError('Error getting Status'));
  }
}

export function* watchGetStatus() {
  yield takeEvery(STATUS_ALL_GET, getStatus);
}

export default function* rootSaga() {
  yield all([fork(watchGetStatus)]);
}
