// eslint-disable-next-line import/no-cycle
import {
  ACCOUNT_UPDATE_FILTERS,
  ACCOUNT_GET_USERS,
  ACCOUNT_GET_USERS_SUCCESS,
  ACCOUNT_GET_USERS_ERROR,
} from '../actions';

export const updateFilters = (filters) => ({
  type: ACCOUNT_UPDATE_FILTERS,
  payload: { filters },
});
export const getUsers = (accessToken, filters) => ({
  type: ACCOUNT_GET_USERS,
  payload: { accessToken, filters },
});
export const getUsersSuccess = (data) => ({
  type: ACCOUNT_GET_USERS_SUCCESS,
  payload: { data },
});
export const getUsersError = (message) => ({
  type: ACCOUNT_GET_USERS_ERROR,
  payload: { message },
});
