import { all, call, fork, put, takeEvery } from 'redux-saga/effects';
import instance from '../../helpers/axiosInstance';

import { ACCOUNT_GET_USERS } from '../actions';

import { getUsersSuccess, getUsersError } from './actions';

const getUsersAsync = async (accessToken, filters) => {
  const response = await instance({
    url: `/users/get-user?pageSize=${filters.selectedPageSize}&orderBy=${filters.selectedOrderOption.column}&currentPage=${filters.currentPage}&search=${filters.search}`,
    method: 'GET',
    headers: {
      Authorization: accessToken,
    },
  });

  return { ...response.data };
};

function* getUsers({ payload }) {
  const { accessToken, filters } = payload;
  try {
    const accounts = yield call(getUsersAsync, accessToken, filters);
    yield put(getUsersSuccess(accounts.data));
  } catch (error) {
    yield put(getUsersError('Error getting Users'));
  }
}

export function* watchGetUsers() {
  yield takeEvery(ACCOUNT_GET_USERS, getUsers);
}

export default function* rootSaga() {
  yield all([fork(watchGetUsers)]);
}
