// eslint-disable-next-line import/no-cycle
import {
  SUB_CATEGORY_ALL_GET,
  SUB_CATEGORY_ALL_GET_ERROR,
  SUB_CATEGORY_ALL_GET_SUCCESS,
  SUB_CATEGORY_UPDATE_FILTERS,
} from '../actions';

export const updateSubCategoryFilters = (filters) => ({
  type: SUB_CATEGORY_UPDATE_FILTERS,
  payload: { filters },
});
export const getAllSubCategory = (accessToken, filters) => ({
  type: SUB_CATEGORY_ALL_GET,
  payload: { accessToken, filters },
});
export const getAllSubCategorySuccess = (data) => ({
  type: SUB_CATEGORY_ALL_GET_SUCCESS,
  payload: { data },
});
export const getAllSubCategoryError = (message) => ({
  type: SUB_CATEGORY_ALL_GET_ERROR,
  payload: { message },
});
