// eslint-disable-next-line import/no-cycle
import {
  CLIENT_UPDATE_FILTERS,
  CLIENT_GET_USERS,
  CLIENT_GET_USERS_SUCCESS,
  CLIENT_GET_USERS_ERROR,
  CLIENT_GET_BY_ID,
  CLIENT_GET_BY_ID_SUCCESS,
  CLIENT_GET_BY_ID_ERROR,
} from '../actions';

export const updateClientFilters = (filters) => ({
  type: CLIENT_UPDATE_FILTERS,
  payload: { filters },
});
export const getClients = (accessToken, filters) => ({
  type: CLIENT_GET_USERS,
  payload: { accessToken, filters },
});
export const getClientsSuccess = (data) => ({
  type: CLIENT_GET_USERS_SUCCESS,
  payload: { data },
});
export const getClientsError = (message) => ({
  type: CLIENT_GET_USERS_ERROR,
  payload: { message },
});

export const getClient = (accessToken, id) => ({
  type: CLIENT_GET_BY_ID,
  payload: { accessToken, id },
});
export const getClientSuccess = (client, clientServices) => ({
  type: CLIENT_GET_BY_ID_SUCCESS,
  payload: { client, clientServices },
});
export const getClientError = (message) => ({
  type: CLIENT_GET_BY_ID_ERROR,
  payload: { message },
});
